import React, { Component } from 'react'
import { View, TouchableOpacity, Text,StyleSheet } from 'react-native'
import DraggableFlatList from 'react-native-draggable-dynamic-flatlist'
import Collapsible from 'react-native-collapsible';
import { FlatList } from 'react-native-gesture-handler';
class Example extends Component {

  state = {
    data: [...Array(20)].map((d, index) => ({
      key: `item-${index}`,
      label: index,
      backgroundColor: `rgb(${Math.floor(Math.random() * 255)}, ${index * 5}, ${132})`,
    })),
    activeSections: [],
    collapsed: false,
    dataFlat: [
      {
        id: 4,
        title: "Header1",
        data: ["item1", "item2", "item3", "item4"]
      },
      { id: 5, title: "Header2", data: ["item5", "item6", "item7", "item8"] },
      { id: 6, title: "Header3", data: ["item9", "item10", "item11", "item12"] }
    ],
    collapsibleItems: []
  }  
  _setCollapsible = id => {
    // clone ids
    console.log("test");
    console.log(this.state.data);
    const newIds = [...this.state.collapsibleItems];

    // check to add /remove id
    const index = newIds.indexOf(id);
    if (index > -1) {
      newIds.splice(index, 1);
    } else {
      newIds.push(id);
    }

    // set new ids
    this.setState({ collapsibleItems: newIds });
  };

  _renderSectionHeader(section) {
      console.log("header");
      console.log(section);
    return (
      <TouchableOpacity
        style={styles.sectionHeader}
        onPress={() => this._setCollapsible(section.id)}
      >
        <Text style={styles.sectionTitle}>{section.title}</Text>
      </TouchableOpacity>
    );
  }

  _renderSectionItems(section) {
    return (
      <View style={styles.sectionItems}>
        {section.data.map(item => (
          <Text style={styles.itemText} key={item}>
            {item}
          </Text>
        ))}
      </View>
    );
    //return <View style={{ height: 100, backgroundColor: "green" }}></View>;
  }

  renderItemFlat = (data) => {
    return (
      <React.Fragment>
        {this._renderSectionHeader(data.item)}
        <Collapsible collapsed={this.state.collapsibleItems.includes(data.item.id)}>
          {this._renderSectionItems(data.item)}
        </Collapsible>
      </React.Fragment>
    );
  };

  toggleExpanded = () => {
    
    console.log("header");
    console.log(this.state.data);
    this.setState({ collapsed: !this.state.collapsed });
   
  };

  setSections = sections => {
    this.setState({
      activeSections: sections.includes(undefined) ? [] : sections,
    });
  };

  renderItem = ({ item, index, move, moveEnd, isActive }) => {
    return (
        <View>
      <TouchableOpacity
        style={{ 
          height: 100, 
          backgroundColor: isActive ? 'blue' : item.backgroundColor,
          alignItems: 'center', 
          justifyContent: 'center' 
        }}
        onLongPress={move}
        onPressOut={moveEnd}>
        <TouchableOpacity onPress={this.toggleExpanded}>
            <View style={styles.header}>
              <Text style={styles.headerText}>Single Collapsible</Text>
            </View>
          </TouchableOpacity>
      </TouchableOpacity>
          <Collapsible collapsed={this.state.collapsed} align="center">
            <View style={styles.content}>
              <Text>
                Bacon ipsum dolor amet chuck turducken landjaeger tongue spare
                ribs
              </Text>
            </View>
          </Collapsible>
      </View>
    )
  }

  render() {
    return (
      <View style={{ flex: 1 }}>
      <TouchableOpacity onPress={this.toggleExpanded}>
          <View style={styles.header}>
            <Text style={styles.headerText}>Single Collapsible</Text>
          </View>
        </TouchableOpacity>
        <Collapsible collapsed={this.state.collapsed} align="center">
          <View style={styles.content}>
            <Text>
              Bacon ipsum dolor amet chuck turducken landjaeger tongue spare
              ribs
            </Text>
          </View>
        </Collapsible>
        <DraggableFlatList
          data={this.state.data}
          renderItem={this.renderItem}
          keyExtractor={(item, index) => `draggable-item-${item.key}`}
          scrollPercent={5}
          onMoveEnd={({ data }) => this.setState({ data })}
        />
        
      </View>
    )
  }
}

export default Example

const styles = StyleSheet.create({
    container: {
      flex: 1,
      backgroundColor: '#F5FCFF',
    },
    title: {
      textAlign: 'center',
      fontSize: 22,
      fontWeight: '300',
      marginBottom: 20,
    },
    header: {
      backgroundColor: '#F5FCFF',
      padding: 10,
    },
    headerText: {
      textAlign: 'center',
      fontSize: 16,
      fontWeight: '500',
    },
    content: {
      padding: 20,
      backgroundColor: '#fff',
    },
    active: {
      backgroundColor: 'rgba(255,255,255,1)',
    },
    inactive: {
      backgroundColor: 'rgba(245,252,255,1)',
    },
    selectors: {
      marginBottom: 10,
      flexDirection: 'row',
      justifyContent: 'center',
    },
    selector: {
      backgroundColor: '#F5FCFF',
      padding: 10,
    },
    activeSelector: {
      fontWeight: 'bold',
    },
    selectTitle: {
      fontSize: 14,
      fontWeight: '500',
      padding: 10,
    },
    multipleToggle: {
      flexDirection: 'row',
      justifyContent: 'center',
      marginVertical: 30,
      alignItems: 'center',
    },
    multipleToggle__title: {
      fontSize: 16,
      marginRight: 8,
    },
  });